import React, { Component } from 'react';
import PropTypes from 'prop-types';


const propTypes = {

};

const style = {
  color: '#fff'
}

class BackGammonLobby extends Component {
  render() {
    return (
      <div className="BackGammonLobby" style={style}>
        <div className="container">
          <div className="form-group">
            <input type="range" className="form-control " min="2500" max="10000" />
          </div>
          <div className="form-group">
            <button className="form-control btn btn-success">Play for</button>
          </div>
          <div className="form-group">
            <button className="form-control btn btn-success">Play Free</button>
          </div>
        </div>
        <div className="rooms">
          Open Rooms (91 Users Online)
        </div>
      </div>
    );
  }
}


BackGammonLobby.propTypes = propTypes;


export default BackGammonLobby;



